.services{
    // height: 100vh;
    background-color: #fff;
    font-family: 'Montserrat';
    color: #1F373D;
    padding-top: 7%;
    padding-bottom: 7%;
    h2{
        font-weight: 600;
        font-size: 43px;
        margin-bottom: 3%;
    }
    h3{
        font-size: 27px;
    }
    &-content_title{
        margin-bottom: 5%;
    }
    &-content_cards{
        display: flex;
        justify-content: space-between;
        &__card{
            display: flex;
            flex-direction: column;
            background-color: #fff;
            box-shadow: 0 0 20px rgba(0,0,0,0.3);
            overflow: hidden;
            color: #1F373D;
            .card_number{
                font-size: 200px;
                font-weight: bold;
                position: relative;
                top: -140px;
                left: -70px;
            }
            .card_title{
                font-size: 18px;
                font-weight: bold;
                margin: -60% 0 3%;
            }
            .card_content{
                font-size: 13px;
                margin: 0 0 5% 5%;

            }
        }
        &__card:hover{
            color: #fff;
            background-color: #ffcc01;
        }
    }
}